import React from "react";
import linkedin from "../assets/SocialLogos/linkedin.svg";
import gmail from "../assets/SocialLogos/gmail.svg";
import github from "../assets/SocialLogos/github.svg";
import instagram from "../assets/SocialLogos/instagram.svg";

const Socials = () => {
  return (
    <div className="w-11/12 h-40 md:w-2/3 rounded-md flex flex-row justify-evenly items-center py-2">
      <a
        href="https://www.linkedin.com/in/dz365"
        target="_blank"
        rel="noreferrer"
      >
        <img
          src={linkedin}
          className="w-8 md:w-16 transform duration-500 hover:scale-125"
          alt="LinkedIn"
        />
      </a>
      <a href="https://www.github.com/dz365" target="_blank" rel="noreferrer">
        <img
          src={github}
          className="w-8 md:w-16 transform duration-500 hover:scale-125"
          alt="GitHub"
        />
      </a>
      <a href="mailto:dwqz365@gmail.com" target="_blank" rel="noreferrer">
        <img
          src={gmail}
          className="w-8 md:w-16 transform duration-500 hover:scale-125"
          alt="Email"
        />
      </a>
      <a
        href="https://www.instagram.com/dz.2372"
        target="_blank"
        rel="noreferrer"
      >
        <img
          src={instagram}
          className="w-8 md:w-16 transform duration-500 hover:scale-125"
          alt="Instagram"
        />
      </a>
    </div>
  );
};

export default Socials;
