import React from "react";
import Project from "./projects";
import asteroids from "../assets/Projects/Asteroids.png";
import javmos from "../assets/Projects/Javmos.png";
import digitrecognizer from "../assets/Projects/DigitRecognizer.png";
import easyapply from "../assets/Projects/EasyApply.png";
import sixdegrees from "../assets/Projects/SixDegrees.png";

const ProjectSection = () => {
  return (
    <div className="w-full flex flex-col justify-center items-center">
      <p className="text-3xl my-4">Projects</p>
      <div className="xl:w-1/2 md:w-2/3 grid lg:grid-cols-2 xl:grid-cols-3 gap-3">
        <Project name="Easy Apply" img={easyapply} />
        <Project name="6 Degrees of Kevin Bacon" img={sixdegrees} />
        <Project name="Digit Decognizer" img={digitrecognizer} />
        <Project name="Asteroids" img={asteroids} />
        <Project name="Javmos" img={javmos} />
      </div>
    </div>
  );
};

export default ProjectSection;
