import React from "react";
import quotation from "../assets/quotation.png";
import resume from "../assets/Resume.pdf";
const About = () => {
  return (
    <div className="w-11/12 md:w-2/3 relative p-4 border-2 border-gray-100 p-4 flex flex-col justify-center items-center">
      <div className="w-full">
        <img src={quotation} alt="" className="w-16 transform rotate-180" />
      </div>

      <p className="w-2/3 text-xl">
        <span className="font-bold">Hey There!</span> Thanks for visiting my
        webpage 🙂 . <br /> My name is Daniel Zhang and I'm a Computer Science
        student currently studying at University of Toronto. Below are some work
        experiences and projects completed as a programmer. I've also listed
        some of the programming languages I'm familiar with. Feel free to
        continue scrolling and if you want to have a quick chat, just contact me
        at one of the socials listed above. If you are a recruiter, my resume is
        available right below. ↓↓↓
      </p>
      <a
        href={resume}
        target="_blank"
        rel="noreferrer"
        className="mt-4 text-lg text-blue-500"
      >
        Resume.pdf
      </a>
      <div className="w-full flex justify-end">
        <img src={quotation} alt="" className="w-16" />
      </div>
    </div>
  );
};

export default About;
